{
  color: lightskyblue;
  font-size: 20px;
  display: inline-block;
  padding: 0px 20px;
}

.first p 
{
  margin: 5px 0px 0px 25px;
  width: 60%;
  color: #7f7f7f;
  text-align: left;
}

.first img
{
  width: 80%;
  margin: 0 10%;
}

.second p
{
  text-align: left;
}

.price 
{
  width: 150px;
  position: absolute;
  top: 30px;
  right: 20px;
  border-bottom: 1px solid #19b9ac;
}

.price span 
{
  font-size: 40px;
  color: #7f7f7f;
}

.second 
{
  font-family: 'Cantarell', sans-serif;
}

.second p
{
